.text-box-container {
  padding: 5px;
  padding-left: 12px;
  padding-right: 13px;
}

.form-label{
  font-size: 13px;
  text-indent: 50px;
  font-weight: bold;
}

.input {
  border: 0px solid #ccc;
  border-radius: 10px;
  box-sizing: border-box;
  background-color: #F0F2F4;
  min-height: 40px;
  width: 100%;
  text-indent: 10px;
  font-weight: bold;
  font-size: 13px;
  color: #353535;
  margin-top: 10px;
}

.visibility-icon-container{
  display: flex;
  justify-content: flex-end;
  height: 0px;
}

.visibility-icon{
  position: relative;
  bottom: 30px;
  right: 10px;
}

